import * as React from 'react';
import { BambooPlaceholder as Placeholder } from '@ali/4ever-component';
import { Card } from '@ali/4ever-plugin-card';
import { RenderNodeProps } from '@ali/4ever-cangjie';
import { moFactory } from '@ali/4ever-plugin-card';

const renderUnknownCard = {
  [Card.TYPE]: (props: RenderNodeProps) => {
    return (<Placeholder {...props} />);
  },
};

const createUnknownCardPlugin = () => ({
  ...moFactory({}),
  renderNode: renderUnknownCard,
});

export default createUnknownCardPlugin;
